.NavParent{
    width:100vw;
    top:0;
    left:0;
    right:0;
    overflow-x: auto;
    
}

.NavChild{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:0.2cm 0.3cm;
    border-bottom: thin solid rgb(53, 53, 53);
}

.Menu{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items: center;
}

.LogoImg{
    width:6cm;
    height:auto;
}

.itemLight{
    font-family: Georgia, 'Times New Roman', Times, serif;
    color:rgb(28, 28, 28);
    font-size: 16px;
    text-decoration: none;
}

.Dark{
    color:rgb(238, 238, 238);
    font-size: 16px;
}

.signOutBtn{
    width:0.8cm;
    height: auto;
    cursor: pointer;
}

.active{
    color:rgb(137, 0, 217);
}